$confirmed-color: #448aff;
$death-color: #ff5252;
$recovered-color: #b2ff59;
$active-color: #ffff00;

.App {
  text-align: center;
}

.main-container {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;

  .header {
    margin-top: 10px;
    .title {
      font-size: 50px;
      font-weight: bold;
      margin-left: 160px;
    }
    .date {
      float: right;
      font-size: 24px;
      line-height: 66px;
      font-weight: 400;
      margin-right: 30px;
    }
  }
  
  .display-flex {
    display: flex;
  }

  
  .card-block {
    width: 50%;
    padding: 0 15px;
    margin: 25px 0;
    border-right: 1px solid whitesmoke;
    &.no-boder {
      border: 0;
    }
    .card-container-title {
      margin-bottom: 20px;
      font-size: 40px;
      font-weight: 200;
    }
    .card-container {
      display: flex;
      .cards {
        margin: 0 10px;
        border-bottom: 2px solid whitesmoke;
        width: 25%;
        height: 100px;
        .count {
          font-size: 40px;
          font-weight: 600;
        }
        .confirmed-card {
          color: $confirmed-color;
          font-weight: 500;
        }
      
        .death-card {
          color: $death-color;
          font-weight: 500;
        }
      
        .recovered-card {
          color: $recovered-color;
          font-weight: 500;
        }
      
        .active-card {
          color: $active-color;
          font-weight: 500;
        }
        &:hover {
          border: 2px solid whitesmoke;
        }
      }
    }
  }

  .details-container {
    display: flex;
    .left-panel{
      width: 20%;
      .country-list {
        height: 350px;
        overflow-y: auto;
        .country-row {
          font-size: 18px;
          text-align: left;
          border-bottom: 1px solid whitesmoke;
          padding: 12px;
          &:hover {
            color: $confirmed-color;
            cursor: pointer;
          }
        }
      }
    }
    .right-panel {
      width: 80%;
    }
  }
}

